import React from 'react'
import { Link } from 'gatsby'

/*
ListLink defines the component which will show the links inline
*/
const ListLink = props => (
  <li style={{ display: 'inline-block', marginRight: '1rem' }}>
    <Link to={props.to}>{props.children}</Link>
  </li>
)

// Header is the exported React Component that contains ListLinks
const Header = ({ siteTitle }) => (
  // Parent section showing the color bar
  <div
    style={{
      background: '#2f3143',
      marginBottom: '1.45rem',
    }}
  > 
    {/* The section below is for the White title which is a link too
     */}
    <div
      style={{
        margin: '0 auto',
        maxWidth: 960,
        padding: '1.45rem 1.0875rem',
      }}
    >
      <h1 style={{ margin: 0 }}>
        <Link
          to="/"
          style={{
            color: 'white',
            textDecoration: 'none',
          }}
        >
          {siteTitle}
        </Link>
      </h1>
    </div>
          
    {/* This section is for the 3 links Home, About, Contact */}
    <header style={{ marginBottom: `0rem` }}>
      <ul style={{ listStyle: `none`, float: `right` }}>
        <Link to="/about/">About</Link>
        {/* <Link to="/contact">Contact</Link> */}
        {/* <ListLink to="/">Home</ListLink>
        <ListLink to="/about/">About</ListLink>
        <ListLink to="/contact/">Contact</ListLink> */}
      </ul>
    </header>

  </div>
)

export default Header
